// export default Blog;
import BlogClient from '@/app/blog/[slug]/BlogClient';
  
import {  getBlogPostBySlug } from '@/lib/sanity';

interface PageProps {
  params: { slug: string };
}

export default async function BlogPage({ params }: PageProps) {
  const slug = params.slug;
  // optional: fetch blog post on server
  const blogPost = await getBlogPost(slug);

  return <BlogClient slug={slug} blogPost={blogPost} />;
}
